import React, {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import {GlobalStateType} from "../../../redux/store-redux";
import {useLocation} from "react-router";
import {appActions} from "../../../redux/app-reducer";

const GetUrlPatch: React.FC = (() => {
    console.log( "GetUrlPatch" )

    const {setPatch} = appActions

    const dispatch = useDispatch();

    const patch: string = useSelector( (state: GlobalStateType) => state.app.patch )

    const location = useLocation()

    useEffect( () => { //          

        if (!(patch !== "" && location.pathname.includes( patch ))) {
            const patchArray = location.pathname //   URL  /profile
                .split( "" ) //     ['/', 'd', 'i', 'a', 'l', 'o', 'g', 's', '/', '2', '8', '8', '3', '1',]
            const tempPath: Array<String> = [] //   
            for (let i: number = 1; i < patchArray.length; i++) { //    ,    '/'
                if (patchArray[i] === '/') {
                    break //  ,   /
                }
                tempPath.push( patchArray[i] ) //    
            }
            const UpdatedPatch: string = tempPath.join( "" ) //  
            console.log("dispatch UpdatedPatch:", UpdatedPatch)
            dispatch( setPatch( UpdatedPatch ) )//    patch  app-reducer

        }


    }, [location, setPatch, patch] )

    return <>
    </>
})
export default GetUrlPatch
